

























































import { Component, Vue } from 'vue-property-decorator';
import { fetch } from '../../service/api';
import { VueRouter } from 'vue-router/types/router';
import LocalStorage from '../../service/localStorage';
import Auth from '@/service/auth';
@Component({})
export default class Index extends Vue {
    ruleForm = {
        account: '',
        password: '',
    };
    router!: VueRouter;
    test = '即构直播课堂';
    disable = true;
    logo = require('../../assets/images/Logo.png');
    bg = require('../../assets/images/ManageBg.png');
    del = require('../../assets/images/del.png');
    rules = {
        account: [{ required: true, message: '请输入账号', trigger: 'blur' }],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
    };
    // method
    public submitForm(ruleForm: any): void {
        const el: any = this.$refs[ruleForm];
        el.validate((valiRes: any) => {
            if (valiRes) {
                this.login(this.ruleForm);
            }
        });
    }
    public login(data: any): void {
        Auth.classLogin(data.account, data.password)
            .then((res: any) => {
                if (res.ret.code == 200) {
                    if (res.msg.code == 200) {
                        let auth = { user_id: res.data.user_id, title: res.data.title, appId: res.data.app_id }; // 新需求
                        // let auth = { user_id: res.data.user_id, title: res.data.title };
                        LocalStorage.update('auth', auth);
                        this.router.push({ path: '/room/' });
                    } else {
                        this.$notify.error({
                            title: '错误',
                            message: '账号或密码错误',
                        });
                    }
                }
            })
            .catch(error => {
                this.$notify.error({
                    title: '错误',
                    message: '服务器内部错误',
                });
            });
    }
    remake() {
        this.ruleForm.account = '';
    }
    mounted() {
        this.router = this.$router as VueRouter;
    }
}
